.topbar{
    width:100%;
    height: 50px;
    border-bottom: rgb(194, 192, 192);
    border-bottom-style: solid;
    border-width: thin;
}

.topbarContainer {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.title {
    font-size: 30px;
    font-weight: bold;
    font-family: 'Arial';
}





