
.convertContainer {
background: #f0f0f0;
box-sizing: border-box;
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
width: 100vw;
height: 100vh;
}

h2 {
    margin-bottom: 30px;
    position: relative;
}
  
.box {
border-radius: 20px;
width: 60px;
height: 60px;
background: #f0f0f0;
color: #f0f0f0;
box-shadow:  0 0 0 #cccccc,
                0 0 0 #ffffff,
                10px 10px 10px #cccccc inset,
                -10px -10px 10px #ffffff inset;
/*animation: anime 3s cubic-bezier(0.16, 1, 0.3, 1) 1s infinite alternate;*/
animation: anime 2s cubic-bezier(0.8, 0.5, 0.15, 0.5) 1 normal forwards;
/* animation-fill-mode: forwards; */
}

@keyframes anime {
0% {
    width: 60px;
    height: 60px;
    background: #f0f0f0;
    color: #f0f0f0;
    box-shadow:  0 0 0 #cccccc,
                0 0 0 #ffffff,
                10px 10px 10px #cccccc inset,
                -10px -10px 10px #ffffff inset;

}
25% {
    width: 60px;
    height: 60px;
    background: #f8f8f8;
    color: #f8f8f8;
    box-shadow:  10px 10px 10px #cccccc,
                10px 10px 10px #ffffff,
                0 0 0 #cccccc inset,
                0 0 0 #ffffff inset;
}
50% {
    width: 60px;
    height: 240px;
    background: #f8f8f8;
    color: #f8f8f8;
    box-shadow:  10px 10px 10px #cccccc,
                10px 10px 10px #ffffff,
                0 0 0 #cccccc inset,
                0 0 0 #ffffff inset;
}
100% {
    width: 580px;
    height: 240px;
    background: #fafafa;
    color: black;
    box-shadow:  40px 40px 40px #cccccc,
                0 0 0 #ffffff,
                0 0 0 #cccccc inset,
                2px 2px 2px #ffffff inset;
}
}

input, 
select  {
    
    animation: inputanime 2s cubic-bezier(0.8, 0.5, 0.15, 0.5)1 normal forwards;
}

@keyframes inputanime {
    0% {
        visibility: hidden;
    }
    25% {
        visibility: hidden;
    }
    50% {
        visibility: hidden;
        width: 0px;
        
    }

    100% {
        
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        -webkit-appearance: none;
        box-sizing: border-box;
        height: 30px;
        border-width:0px;
        border:none;
        border-radius: 5px;
        width: 250px;
    }
}

label, 
#convertSymbol{
    animation: textanime 2s cubic-bezier(0.8, 0.5, 0.15, 0.5) 1 normal forwards;
    visibility: hidden;
}

@keyframes textanime {
    0% {
        visibility: hidden;
        
    }
    25% {
        visibility: hidden;
        
    }
    50% {
        visibility: hidden;
        width: 0px;
    }
    100% {
        visibility: visible;
        width: 100%;
    }
}

.fromContainer,
.toContainer {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-content: space-around;
    
}

.fromContainer {
    margin-top: 5px;
}

.converterInput {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

#bruh {
    pointer-events: none;
}

#convertValue {
    pointer-events: none;
}

#convertSymbol {
    text-align: center;
}